.rightbar {
	flex: 3;
	height: calc(100vh - 50px);
	overflow-y: scroll;
	position: sticky;
	top: 50px;
}

.rightbarWrapper {
	padding: 20px 20px 0 0;
}

.birthdayContainer {
	display: flex;
	align-items: center;
}

.birthdayImg {
	width: 40px;
	height: 40px;
	margin-right: 10px;
}

.birthdayText {
	font-weight: 300;
	font-size: 15px;
}

.rightbarAd {
	width: 100%;
	border-radius: 10px;
	margin: 30px 0;
}

.rightbarTitle {
	margin-bottom: 20px;
}

.rightbarFriendList {
	list-style: none;
}

.rightbarFriend {
	display: flex;
	align-items: center;
	margin-bottom: 15px;
}

.rightbarProfileImgContainer {
	position: relative;
}

.rightbarProfileImg {
	width: 40px;
	height: 40px;
	border-radius: 50%;
	object-fit: cover;
}

.rightbarOnline {
	width: 12px;
	height: 12px;
	border-radius: 50%;
	background-color: limegreen;
	position: absolute;
	top: -2px;
	right: 0;
	border: 2px solid white;
}

.rightbarUsername {
	font-weight: 500;
	margin-left: 10px;
}

.rightbarTitle {
	font-size: 18px;
	font-weight: 500;
	margin-bottom: 10px;
}

.rightbarInfo {
	margin-bottom: 30px;
}

.rightbarInfoItem {
	margin-bottom: 10px;
}

.righbarInfoKey {
	font-weight: 500;
	margin-right: 15px;
	color: #555;
}

.righbarInfoValue {
	font-weight: 500;
}

.rightbarFollowings {
	display: flex;
	flex-wrap: wrap;
	/* justify-content: space-between; */
}

.rightbarFollowing {
	display: flex;
	flex-direction: column;
	margin-bottom: 20px;
	margin-right: 10px;
}

.rightbarFollowingImg {
	width: 100px;
	height: 100px;
	object-fit: cover;
	border-radius: 5px;
}

.rightbarFollowingName {
	margin: 0 auto;
}

.rightbarFollowButton {
	margin-top: 30px;
	margin-bottom: 10px;
	border: none;
	background-color: #1872f2;
	color: white;
	border-radius: 5px;
	display: flex;
	/* justify-content: center; */
	align-items: center;
	padding: 5px 10px;
	font-size: 16px;
	font-weight: 500;
	cursor: pointer;
}

.rightbarFollowButton:hover {
	background-color: green;
}

.rightbarFollowButton:focus {
	outline: none;
}
